@import "sass/varialble";
@import "sass/function";

.clients {
  height: 227px;
  @include desktopSmall {
    height: 165px; } }

.clients__wrap {
  margin-top: 70px;
  @include desktopSmall {
    margin-top: 25px; } }

.clients__slider {
  width: 80%; }

.clients__arrow {
  cursor: pointer; }

.clients__item {
  cursor: pointer;
  @include tabletSmall {
    &:nth-child(3), &:nth-child(4) {
      display: none; } } }
